.dropdown-menu{
    width: 200px;
    position:absolute;
    top: 80px;
    list-style: none;
    text-align: start;
}

.dropdown-menu li {
    background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
    color: #fff;
    cursor: pointer;
    
}

.dropdown-menu li:hover{
    background: #ffae00;
}

.dropdown-menu.clicked {
    display: none;
}

.dropdown-link{
    display: block;
    width: 100%;
    height: 100;
    text-decoration: none;
    color: #FFF;
    padding: 16px;
}
@media screen and (max-width: 960px) {
    .fa-caret-down {
    display: none;
    }
}